import React from "react";

import Heraldry from "./Heraldry";
import heraldry from "./data/heraldry";

import "./Heraldries.css";

function Heraldries(props) {
  const { filter = "" } = props;

  const heraldriesDOM = React.useMemo(() => {
    return heraldry.reduce((heraldriesDOM, info) => {
      const isVisible = info.name.toLowerCase().includes(filter.toLowerCase());

      heraldriesDOM.push(
        <Heraldry key={info.name} name={info.name} visible={isVisible} />
      );

      return heraldriesDOM;
    }, []);
  }, [filter]);

  return (
    <div className="Heraldries">
      {heraldriesDOM.length === 0 ? "No Match" : heraldriesDOM}
    </div>
  );
}

export default React.memo(Heraldries);
