import React from "react";

import "./Filter.css";

function Filter(props) {
  const { value, onChange: onChangeProp } = props;

  const onChange = React.useCallback(
    (ev) => {
      onChangeProp(ev.target.value);
    },
    [onChangeProp]
  );

  return (
    <input
      className="Filter"
      type="text"
      placeholder="Filter Heraldry"
      value={value}
      onChange={onChange}
    />
  );
}

export default React.memo(Filter);
