import React from "react";

import "./Heraldry.css";

function Heraldry(props) {
  const { name, visible = true } = props;

  const classes = React.useMemo(() => {
    const classes = ["Heraldry"];

    if (visible === false) classes.push("Heraldry--Hidden");

    return classes.join(" ");
  }, [visible]);

  return (
    <figure className={classes}>
      <img
        width={"100%"}
        className="HeraldryImage"
        alt={`${name} heraldry`}
        src={`/images/${name}.png`}
      />
      <figcaption>{name}</figcaption>
    </figure>
  );
}

export default React.memo(Heraldry);
